.hero-slider {
  min-height: 360px;
  background: #212121;
  /* overflow: hidden; */
  color: #fff;
}

@media screen and (max-width: 768px) {
  .hero-slider {
      min-height: 580px;
  }
}

@media screen and (max-width: 576px) {
  .hero-slider h2{
      font-size: 24px;
  }
  
  .hero-slider h5{
      font-size: 16px;
  }
}

.send-email-btn {
  border: 1px solid #D04F45;
}

.send-email-btn:hover {
  border: 1px solid #D04F45;
  background: #D04F45;
}

.hero {
  position: relative;
  /* margin-top: 48px; */
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  /* height: 100vh; */
  height: 50vh;
  color: #ffffff;
  background: #000 url(./robo-hand.jpg) no-repeat center center;
}

.hero-block {
  padding-top: 36px;
  position: relative;
  z-index: 5;
  min-width: 320px;
}

.element {
  white-space: pre;
  min-height: 40px;
}

.particles {
  /* background-color: green; */
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.project-card {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid gray;
  border-radius: 50%;
  background-color: #2d2d2d;
  width: 260px;
  height: 260px;
}

.modal-header, .modal-footer {
  border: none;
}

.popup-checkbox-label {
  font-size: 14px;
}

.popup-checkbox-label a {
  color: #D04F45;
}

.popup-checkbox {
  margin-right: 12px;
}
.hide {
  display: none;
}

.bg-black {
  background-color: #000000;
}
.bg-dark {
  background-color: #262626 !important;
}